<template>
  <form>
    <md-card>
      <md-card-header :data-background-color="dataBackgroundColor">
        <h4 class="title">Редактирование задачи</h4>
        <p class="category">Complete your profile</p>
      </md-card-header>

      <md-card-content>
        <div class="md-layout">
          <div class="md-layout-item md-small-size-100 md-size-100">
            <md-field>
              <label>Название задачи</label>
              <md-input v-model="title"></md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-size-100">
            <md-field maxlength="5">
              <label>Описание</label>
              <md-textarea v-model="description"></md-textarea>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field>
              <label>Приоритет</label>
              <md-select name="priority  " id="priority">
                <md-option
                  v-for="one in priority"
                  :value="one.value"
                  v-bind:key="one.value"
                >{{ one.name }}</md-option>
              </md-select>
            </md-field>
          </div>

            <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field>
              <label>Категория</label>
              <md-select name="category  " id="category">
                <md-option
                  v-for="one in category"
                  :value="one.value"
                 v-bind:key="one.value"
                >{{ one.name }}</md-option>
              </md-select>
            </md-field>
          </div>
         
  <div class="md-layout-item md-small-size-100 md-size-100">
    <label>Дата начала</label>
            <md-field>
              <!-- <datepicker></datepicker> -->
              <md-datepicker  v-model="dateStart" :md-open-on-focus="false" :md-immediately="true"></md-datepicker>
            </md-field>
          </div>

           <div class="md-layout-item md-small-size-100 md-size-100">
             <label>Дата завершения</label>
            <md-field>
              <md-datepicker  v-model="dateEnd" :md-open-on-focus="false" :md-immediately="true"></md-datepicker>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-100">
            <label>Приложенные файлы</label>
            <md-field>
              
              <md-input v-model="file" type='file'></md-input>
            </md-field>
          </div>

    

          <div class="md-layout-item md-size-100 text-right">
            <md-button class="md-raised md-success">Сохранить</md-button>
          </div>
        </div>
      </md-card-content>
    </md-card>
  </form>
</template>
<script>
// this.dateFormat = 'yyyy/MM/dd'
export default {
  name: "edit-task",
  props: {
    dataBackgroundColor: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      // dateFormat : 'yyyy/MM/dd',
     ru: 'ru',
      dateStart: null,
      dateEnd: null,
      file: null,
      priority: [
        {
          name: "Низкий",
          value: 1
        },
        {
          name: "Средний",
          value: 2
        },
        {
          name: "Высокий",
          value: 3
        },
        {
          name: "Наивысший",
          value: 4
        }
      ],
      category:  [
        {
          name: "Перв",
          value: "1"
        },
        {
          name: "Втор",
          value: "2"
        }
      ],
      username: null,
      title: null,
      emailadress: null,
      lastname: null,
      firstname: null,
      address: null,
      city: null,
      country: null,
      code: null,
      description: "Пиши"
    };
  },
   computed: {
     
     dateFormat () {
        return 'yyyy/MM/dd'
      }
   }
};

</script>
<style></style>
